import {v1} from 'uuid';

export type DialogsType = {
    id: string
    name: string
    img: string
}
export type MessagesType = {
    id: string
    message: string
}
export type DialogsDataType = Array<DialogsType>
export type MessagesDataType = Array<MessagesType>
export type DialogsPageType = {
    dialogsData: DialogsDataType
    messagesData: MessagesDataType
}
export type AddMessageActionType = {
    type: typeof ADD_MESSAGE
    newMessageBody: string
}

export type DialogsActionsType =
    AddMessageActionType

let initialState: DialogsPageType = {
    dialogsData: [
        {
            id: v1(),
            name: 'Jeka',
            img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTKRCfg2xIu9yLGDGiAXcw56FM5zjIvvCPsfQ&usqp=CAU'
        },
        {
            id: v1(),
            name: 'Kate',
            img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTKRCfg2xIu9yLGDGiAXcw56FM5zjIvvCPsfQ&usqp=CAU'
        },
        {
            id: v1(),
            name: 'Dima',
            img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTKRCfg2xIu9yLGDGiAXcw56FM5zjIvvCPsfQ&usqp=CAU'
        },

    ],
    messagesData: [
        {id: v1(), message: 'Hi'},
        {id: v1(), message: 'How are you?'},
        {id: v1(), message: 'I am good'},
    ],
}

const ADD_MESSAGE = 'dialogs/ADD-MESSAGE'

export const dialogsReducer = (state: DialogsPageType = initialState, action: DialogsActionsType): DialogsPageType => {


    switch (action.type) {
        case ADD_MESSAGE: {
            let newMessage: MessagesType = {
                id: v1(),
                message: action.newMessageBody
            }
            return {
                ...state,
                messagesData: [...state.messagesData, newMessage],
            }
        }
        default:
            return state
    }
}

export const addMessageCreator = (newMessageBody: string): AddMessageActionType => ({type: ADD_MESSAGE, newMessageBody})
